<template>
    <div class="base">
        <form  id="form" enctype="multipart/form-data"  @submit.prevent="submitForm">
            <!-- 标题 -->
            <h1 class="weight-title grey">
				<span>
					添加卡牌
				</span>
            </h1>
            <!-- 列表1 -->
            <ul class="product-boxlist">
                <li class="nobottom">
                    <div>
                        <div class="content">
                            <div class="content-right auto">
                                <div class="content-single all">
                                    <div class="content-singlewrap">
                                        <ul class="content-singlelist">
                                            <li class="column-3">
                                                <p>
                                                    <label>牌阵名称：</label>
                                                    <input placeholder="请输入名称" class="cyan" type="text" v-model="detail.title">

                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p >
                                                    <label>牌阵分类：</label>
                                                    <select v-model="detail.class_id" @change="changeClass">
                                                        <option :value="item.id" v-for="item in select" >{{ item.class_name }}</option>
                                                    </select>
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>标签：</label>
                                                    <input placeholder="请输入标签" class="cyan" type="text" v-model="detail.tag">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>卡牌：</label>
                                                    <input type="file" id="file-2" accept="image/*"  data-path="path_1" @change="upFile"  />
                                                    <label class="vertical-img" for="file-2"><img :src="path_1"></label>
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>段落：</label>
                                                    <textarea type="text" class="no-flex"  v-model="detail.para">
													</textarea>
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>内容：</label>
                                                    <textarea type="text" class="no-flex"  v-model="detail.content">
													</textarea>
                                                </p>
                                            </li>
                                            <li  class="nobottom">
                                                <div class="button-group">
                                                    <button class="button">
                                                        提交
                                                    </button>
                                                </div>
                                            </li>
                                        </ul>
                                        <div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </form>
    </div>
</template>

<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "CardArrayAdd",
        data : function () {
            return {
                select : [],
                detail : {
                    title : '',
                    tag : '',
                    image_face : '',
                    image_detail :'',
                    content : '',
                    class_id : 1,
                    para : ''
                },
                path_1 : ''
            }
        },
        methods:{
            getCardDetail:function ( id ) {
                var _this = this;
                request.post('card_array_detail', { id : id }  ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ) {
                        _this.detail = data.detail;
                        _this.path_1 = _this.detail.image_face;
                    }
                })
            },
            upFile : function(e){
                console.log(e);
                let pathName = e.target.dataset.path ;
                var _this = this;
                var file = e.target.files[0];
                let reader = new FileReader();
                reader.onload = function (e) {
                    var size = parseInt( file.size /1024 );
                    if ( size > 1000 ){
                        alert('上传图片大小不允许超过1M');
                        return;
                    }
                    if ( pathName == 'path_1'){
                        _this[pathName] = e.target.result;
                        _this.detail.image_face = e.target.result;
                    }else{
                        _this[pathName] = e.target.result;
                        _this.detail.image_detail = e.target.result;
                    }

                };
                reader.readAsDataURL(file);
            },
            getClass : function () {
                var _this = this;
                request.get('card_array_class','' ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ){
                        _this.select = data.list ;
                    }else{
                        console.log(data.message);
                    }
                })
            },
            changeClass : function () {
                console.log( this.detail.class_id);
            },
            deletePara : function( index ){
                this.contentList.splice( index , 1 );
                console.log(this.contentList);
            },
            addPara :function(){
                this.contentList.push({ section_title : '标题'  ,  content : '描述' });
            },
            submitForm : function () {
                let _this = this;
                request.post('card_array_list_edit', this.detail ,function (data) {
                    console.log(data);
                })
            }
        },

        created() {

            if ( this.$route.params.id != undefined ){
                this.getCardDetail(this.$route.params.id);
            }
            this.getClass();
        }
    }
</script>

<style scoped>

</style>
