<template>
    <div class="base">
        <form  id="form" enctype="multipart/form-data"  @submit.prevent="submitForm">
            <!-- 标题 -->
            <h1 class="weight-title grey">
				<span>
					添加卡牌
				</span>
            </h1>
            <!-- 列表1 -->
            <ul class="product-boxlist">
                <li class="nobottom">
                    <div>
                        <div class="content">
                            <div class="content-right auto">
                                <div class="content-single all">
                                    <div class="content-singlewrap">
                                        <ul class="content-singlelist">
                                            <li class="column-3">
                                                <p>
                                                    <label>标题：</label>
                                                    <input placeholder="请输入名称" class="cyan" type="text" v-model="detail.title">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>名言：</label>
                                                    <input placeholder="请输入标签" class="cyan" type="text" v-model="detail.saying">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>农历日期：</label>
                                                    <input  placeholder="请输入标签"  class="cyan" type="text" v-model="detail.calender">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>阳历日期：</label>
                                                    <input  placeholder="请输入标签"  class="cyan" type="text" v-model="detail.date_string">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>卡牌封面：</label>
                                                    <input type="file" id="file-2" accept="image/*"  data-path="path_1" @change="upFile"  />
                                                    <label class="vertical-img" for="file-2"><img :src="path_1"></label>
                                                </p>
                                            </li>
                                            <li  class="nobottom">
                                                <div class="button-group">
                                                    <button class="button">
                                                        提交
                                                    </button>
                                                </div>
                                            </li>
                                        </ul>
                                        <div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </form>
    </div>
</template>

<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "CardDay",
        data : function () {
            return {
                select : [],
                detail : {
                    title : '',
                    date_string : '',
                    calender : '',
                    image_detail :'',
                    saying : ''
                },
                path_1 : ''
            }
        },
        methods:{
            getCardDetail:function ( id ) {
                var _this = this;
                request.post('card_day', { id : id }  ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ) {
                        _this.detail = data.detail;
                        _this.path_1 = _this.detail.image_face;
                    }
                })
            },
            upFile : function(e){
                console.log(e);
                let pathName = e.target.dataset.path ;
                var _this = this;
                var file = e.target.files[0];
                let reader = new FileReader();
                reader.onload = function (e) {
                    var size = parseInt( file.size /1024 );
                    if ( size > 1000 ){
                        alert('上传图片大小不允许超过1M');
                        return;
                    }
                    if ( pathName == 'path_1'){
                        _this[pathName] = e.target.result;
                        _this.detail.image_face = e.target.result;
                    }else{
                        _this[pathName] = e.target.result;
                        _this.detail.image_detail = e.target.result;
                    }

                };
                reader.readAsDataURL(file);
            },
            submitForm : function () {
                let _this = this;
                request.post('card_day_edit', this.detail ,function (data) {
                    console.log(data);
                })
            }
        },

        created() {
            this.getCardDetail(1);
        }
    }
</script>

<style scoped>

</style>
