<template>
    <div class="base">
        <h1 class="weight-title ">
			<span>视频列表</span>
            <div class="pro-tag-list">
                <div class="tag pro-tag" name="production-single"><router-link to="/TaroVedioAdd/0"><a >添加视频</a></router-link></div>
                <div class="tag pro-tag" name="production-double">删除视频</div>
            </div>
        </h1>
        <!-- 列表 -->
        <div class="role-list">
            <ul class="vedio-list">
                <li v-for="( item  ) in list">
                    <a @click="introAdd( item.id )">
                        <img :src="item.image_face"/>
                        <span class="title">{{ item .title}}</span>
                    </a>
                </li>
            </ul>
        </div>

        <div class="page-list">
            <a class="first" href="javascript:void(0);">
                首页
            </a>
            <a class="last" href="javascript:void(0);">
                上一页
            </a>
            <a href="javascript:void(0);">
                1
            </a>
            <a href="javascript:void(0);">
                2
            </a>
            <a href="javascript:void(0);">
                3
            </a>
            <a href="javascript:void(0);">
                4
            </a>
            <a href="javascript:void(0);">
                5
            </a>
            <a class="next" href="javascript:void(0);">
                下一页
            </a>
            <a class="pre" href="javascript:void(0);">
                尾页
            </a>
        </div>
    </div>
</template>

<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "TaroVedio" ,
        data : function () {
            return {
                list :[]
            }
        },
        methods : {
            getList:function () {
                var _this = this;
                request.get('taro_video_list', ''  ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ){
                        _this.list = data.list ;
                    }else{
                        console.log(data.message);
                    }
                })
            },
            introAdd : function ( id ) {
                if (id){
                    this.$router.push({
                        path : `TaroVedioAdd/${id}`
                    })
                }
            },
            introDelete :function ( id ) {
                let _this = this;
                request.post('intro_list_delete' , { id : id } , function ( data ) {
                    console.log(data);
                    if ( data.code == 1){
                        _this.getList();
                    }
                })
            }
        },
        created() {
            this.getList();
        }
    }
</script>

