<template>
    <div class="base">
        <form  id="form" enctype="multipart/form-data"  @submit.prevent="submitForm">
            <!-- 标题 -->
            <h1 class="weight-title grey">
				<span>
					添加卡牌
				</span>
            </h1>
            <!-- 列表1 -->
            <ul class="product-boxlist">
                <li class="nobottom">
                    <div>
                        <div class="content">
                            <div class="content-right auto">
                                <div class="content-single all">
                                    <div class="content-singlewrap">
                                        <ul class="content-singlelist">
                                            <li class="column-3">
                                                <p>
                                                    <label>名称：</label>
                                                    <input placeholder="请输入名称" class="cyan" type="text" v-model="detail.title">

                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p >
                                                    <label>卡牌分类：</label>
                                                    <select v-model="detail.class_id" @change="changeClass">
                                                        <option :value="item.id" v-for="item in select" >{{ item.class_name }}</option>
                                                    </select>
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p>
                                                    <label>中文名：</label>
                                                    <input placeholder="请输入中文名" class="cyan" type="text" v-model="detail.card_name">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p>
                                                    <label>英文名：</label>
                                                    <input  placeholder="请输入英文名" class="cyan" type="text" v-model="detail.english_name">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>标签：</label>
                                                    <input placeholder="请输入标签" class="cyan" type="text" v-model="detail.tag">
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>封面：</label>
                                                    <input type="file" id="file-1" accept="image/*"  data-path="path_1" @change="upFile" />
                                                    <label class="vertical-img" for="file-1"><img :src="path_1"></label>
                                                </p>
                                            </li>
                                            <li class="column-3">
                                                <p class="all limit">
                                                    <label>卡牌：</label>
                                                    <input type="file" id="file-2" accept="image/*"  data-path="path_2" @change="upFile"  />
                                                    <label class="vertical-img" for="file-2"><img :src="path_2"></label>
                                                </p>
                                            </li>
                                            <li>
                                                <ul>
                                                    <li class="column-3" v-for="( item ,index ) in contentList">
                                                        <p class="all limit">
                                                            <label>段落2标题：</label>
                                                            <input class="cyan" type="text" v-model="item.section_title">
                                                        </p>
                                                        <p class="all">
                                                            <label>段落3描述：</label>
                                                            <textarea type="text" class="no-flex"  v-model="item.content">
															</textarea>
                                                        </p>
                                                        <p class="all">
                                                            <a class="button" @click="deletePara( index )">
                                                                删除段落
                                                            </a>
                                                        </p>
                                                    </li>
                                                </ul>
                                            </li>
                                            <li  class="nobottom">
                                                <div class="button-group">
                                                    <a class="button" @click="addPara">
                                                        添加段落
                                                    </a>
                                                </div>
                                            </li>
                                            <li  class="nobottom">
                                                <div class="button-group">
                                                    <button class="button">
                                                        提交
                                                    </button>
                                                </div>
                                            </li>
                                        </ul>
                                        <div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </form>
    </div>
</template>

<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "CardAdd",
        data : function () {
            return {
                select : [],
                detail : {
                   title : '',
                   tag : '',
                   image_face : '',
                   image_detail :'',
                   content : '',
                   section_title : '',
                   class_id : 1
                },
                path_1 : '',
                path_2 :'',
                contentList :[
                    { section_title : '标题'  ,  content : '描述' }
                ]

            }
        },
        methods:{
            getCardDetail:function ( id ) {
                var _this = this;
                request.post('card_detail', { id : id }  ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ){
                        _this.detail = data.detail ;
                        _this.path_1 = _this.detail.image_face ;
                        _this.path_2 = _this.detail.image_detail ;
                        if ( _this.detail.content == null ){
                            _this.detail.content = '';
                        }
                        if ( _this.detail.section_title == null ){
                            _this.detail.section_title = '';
                        }
                       let content = _this.detail.content.split('$');
                       let section_title = _this.detail.section_title.split('$');
                       for ( var attr in content){
                           _this.contentList.push({
                               section_title : section_title[attr] ,
                               content : content[attr]
                           });
                       };

                    }else{
                        console.log(data.message);
                    }
                })
            },
            upFile : function(e){
                console.log(e);
                let pathName = e.target.dataset.path ;
                var _this = this;
                var file = e.target.files[0];
                let reader = new FileReader();
                reader.onload = function (e) {
                    var size = parseInt( file.size /1024 );
                    if ( size > 1000 ){
                        alert('上传图片大小不允许超过1M');
                        return;
                    }
                    if ( pathName == 'path_1'){
                        _this[pathName] = e.target.result;
                        _this.detail.image_face = e.target.result;
                    }else{
                        _this[pathName] = e.target.result;
                        _this.detail.image_detail = e.target.result;
                    }

                };
                reader.readAsDataURL(file);
            },
            getClass : function () {
                var _this = this;
                request.get('card_list_class','' ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ){
                        _this.select = data.list ;
                    }else{
                        console.log(data.message);
                    }
                })
            },
            changeClass : function () {
                console.log( this.detail.class_id);
            },
            deletePara : function( index ){
                this.contentList.splice( index , 1 );
                console.log(this.contentList);
            },
            addPara :function(){
                this.contentList.push({ section_title : '标题'  ,  content : '描述' });
            },
            submitForm : function () {
                var length =this.contentList.length -1;
                var section_title = '';
                var content = '';
                console.log(this.contentList);
                console.log(length);
                for ( var i =0 ; i<this.contentList.length ; i++ ){
                    var  symbol = '$';
                    if ( i == length ){
                        symbol = '';
                    };
                     section_title +=  this.contentList[i].section_title + symbol;
                     content += this.contentList[i].content + symbol;
                }
                this.detail.section_title = section_title ;
                this.detail.content = content ;
                console.log(this.detail.section_title);
                console.log(this.detail.content);
                let _this = this;
                request.post('card_list_add', this.detail ,function (data) {
                    console.log(data);
                })
            }
        },

        created() {

           if ( this.$route.params.id != undefined ){
               this.getCardDetail(this.$route.params.id);
           }
            this.getClass();
        }
    }
</script>

<style scoped>

</style>
