<template>
<div class="base">

        <!-- 标题 -->
        <h1 class="weight-title grey">
            <span>
                塔罗分类*添加子类别
            </span>
        </h1>
        <!-- 列表1 -->
        <ul class="product-boxlist">
            <li class="nobottom">

                <form  id="form" enctype="multipart/form-data"  @submit.prevent="submitForm"  >
                <div>
                    <div class="content">
                        <div class="content-right auto">
                            <div class="content-single all">
                                <div class="content-singlewrap">
                                    <ul class="content-singlelist">
                                        <li class="column-3">
                                            <p>
                                                <label>标题：</label>
                                                <input  name="title" class="cyan" type="text" v-model="detail.title" placeholder="请输入标题">
                                            </p>
                                        </li>
                                        <li class="column-3">
                                            <p >
                                                <label>分类：</label>
                                                <select name="class_id"  v-model="detail.class_id" @change="classChange">
                                                    <option  v-bind:value="item.Id" v-for="( item , index ) in select">{{ item.class_name }}</option>
                                                </select>
                                            </p>
                                        </li>
                                        <li class="column-3">
                                            <p>
                                                <label>标签：</label>
                                                <input name="tag" class="cyan" type="text" v-model="detail.tag"  placeholder="请输入标签"/>
                                            </p>
                                        </li>
                                        <li class="column-3">
                                            <p>
                                                <label>封面：</label>
                                                <input name="image_face"  type="file" id="file-1" accept="image/*" @change="upFile" value="detail.image_face" />
                                                <label class="vertical-img" for="file-1"><img :src="path"/></label>
                                            </p>
                                        </li>
                                        <li class="column-3">
                                            <p class="all limit">
                                                <label>内容：</label>
                                                <textarea  type="text" name="content" v-model="detail.content" placeholder="请输入详细介绍">

                                                </textarea>
                                            </p>
                                        </li>
                                        <li  class="nobottom">
                                            <div class="button-group">
<!--                                                <a class="button" @click="submit">-->
<!--                                                    提交-->
<!--                                                </a>-->
                                                <button class="button" >
                                                    提交
                                                </button>
                                            </div>
                                        </li>
                                    </ul>
                                    <div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </form>
            </li>
        </ul>
</div>
</template>
<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "IntroListAdd",
        data : function () {
            return {
                detail : {
                    title : '' ,
                    tag : '',
                    content :'' ,
                    class_id : 1 ,
                    image_face : ''
                },
                select : [],
                path : '',
            }
        },
        methods : {
            classChange : function(){
               console.log(this.detail.class_id);
            },
            submitForm : function () {
                // request.post('intro_detail_edit', this.detail ,function (data) {
                //     console.log(data);
                // });
                let _this = this;
                request.file('intro_detail_edit', this.detail ,function (data) {
                    console.log(data);
                })
            },
            upFile : function(e){
                var _this = this;
                var file = e.target.files[0];
                let reader = new FileReader();
                reader.onload = function (e) {
                    var size = parseInt( file.size /1024 );
                    if ( size > 1000 ){
                        alert('上传图片大小不允许超过1M');
                        return;
                    }
                    _this.path = e.target.result;
                    _this.detail.image_face = e.target.result;
                    // var img = new Image();
                    // img.src = e.target.result;
                    // img.onload = function(){
                     //       var type = file.type;
                    //      var qulity = 1;
                    //     var canvas = document.createElement('canvas');
                    //     var width = img.width * qulity;
                    //     var height = img.height * qulity;//创建canvas元素
                    //     canvas.width= width;
                    //     canvas.height= height;
                    //     canvas.getContext('2d').drawImage( img , 0 , 0 , width , height ); //将图片绘制到canvas中
                    //     var dataURL= canvas.toDataURL(type);
                    //     _this.path = dataURL;
                    //     _this.detail.image_face = dataURL;
                    // };
                };
                reader.readAsDataURL(file);
            },
            getDetail :function( id ){
                let _this = this;
                request.post('intro_detail', { id : id } ,function (data) {
                    if ( data.code == 1 ){
                        _this.detail = data.detail ;
                        _this.path = data.detail.image_face;
                    }else{
                        console.log(data.message);
                    }
                });
            },
            getClass : function(){
                let _this = this;
                request.get('intro_list_child', '' ,function (data) {
                    if ( data.code == 1 ){
                        _this.select = data.list ;
                    }else{
                        console.log(data.message);
                    }
                })
            }
        },
        created() {
            console.log(this.$route.params.id);
            if ( this.$route.params.id !=undefined){
                this.getDetail(this.$route.params.id);
           }
            this.getClass();
        }
    }
</script>

<style scoped>

</style>
