<template>
    <div class="base-background">
        <form>
        <div class="base-login">
            <div class="login-logo">
                <img :src="path"/>
            </div>
            <form>
                <h1 >系统登录</h1>
                <p>
                    <label>用户名 : </label>
                    <input v-model="username" type="text" placeholder="请输入用户名">
                </p>
                <p>
                    <label>密码 : </label>
                    <input  v-model="password" type="text" placeholder="请输入密码">
                </p>
                <p>
                    <a class="login_button" @click="login">提交</a>
                </p>
            </form>
        </div>
       </form>
    </div>
</template>

<script>
    import request  from '../assets/js/ajax.js';
    export default {
        name: "Login" ,
        data : function () {
            return {
                username : '',
                password : '' ,
                path : '../assets/images/login/u19.png'
            }
        },
        methods : {
            login : function () {
                let data = {
                    username : this.username ,
                    password : this.password
                };
                request.post( 'login' , data ,function ( e ) {
                    console.log( e );
                });
            }
        }
    }
</script>

