<template>
    <div class="base">
        <h1 class="weight-title ">
            <span>塔罗类别</span>
            <div class="pro-tag-list">
                <div class="tag pro-tag" name="production-single"><router-link to="/IntroListAdd/0"><a>新增子类</a></router-link></div>
                <div class="tag pro-tag" name="production-double">删除子类</div>
            </div>
        </h1>
        <!-- 列表 -->
        <div class="role-list">
            <table>
                <thead>
                <tr>
                    <th>
                        <input type="checkbox" id="user_all"/>
                        <label  class="user-choose" for="user_all"></label>
                    </th>
                    <th class="middle">方法名称</th>
                    <th class="short">类别</th>
                    <th class="long">标题</th>
                    <th class="long">内容</th>
                    <th class="long">操作</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="( item ) in list ">
                    <td>
                        <input type="checkbox" id="user_1"/>
                        <label class="user-choose" for="user_1"></label>
                    </td>
                    <td>{{ item.title }}</td>
                    <td>{{ item.class_name }}</td>
                    <td>{{ item.tag }}</td>
                    <td>
                        {{ item.content }}
                    </td>
                    <td>
                        <span class="edit">
                                  <a @click="introAdd(item.id)">编辑</a>
                        </span>
                        <span class="delete"><a @click="introDelete(item.id)">删除</a></span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <div class="page-list">
            <a class="first" href="javascript:void(0);">
                首页
            </a>
            <a class="last" href="javascript:void(0);">
                上一页
            </a>
            <a href="javascript:void(0);">
                1
            </a>
            <a href="javascript:void(0);">
                2
            </a>
            <a href="javascript:void(0);">
                3
            </a>
            <a href="javascript:void(0);">
                4
            </a>
            <a href="javascript:void(0);">
                5
            </a>
            <a class="next" href="javascript:void(0);">
                下一页
            </a>
            <a class="pre" href="javascript:void(0);">
                尾页
            </a>
        </div>
    </div>
</template>
<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "IntroList",
        data : function () {
            return {
                list :[]
            }
        },
        methods : {
            getList:function () {
                var _this = this;
                request.get('intro_list','' ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ){
                        _this.list = data.list ;
                    }else{
                        console.log(data.message);
                    }
                })
            },
            introAdd : function ( id ) {
                if (id){
                    this.$router.push({
                        path : `IntroListAdd/${id}`
                    });
                }
            },
            introDelete :function ( id ) {
                let _this = this;
                request.post('intro_list_delete' , { id : id } , function ( data ) {
                      console.log(data);
                      if ( data.code == 1){
                          _this.getList();
                      }
                })
            }
        },
        created() {
            this.getList();
        }
    }
</script>

<style scoped>

</style>
