import vue from 'vue' ;
import Router from 'vue-router';
import HelloWorld from "../components/HelloWorld";
import Card from "../components/Card";//卡牌列表
import CardAdd from "../components/CardAdd";//添加卡牌
import CardArray from "../components/CardArray";//牌阵列表
import CardArrayAdd from "../components/CardArrayAdd";//添加牌阵
import CustomList from "../components/CustomList";//用户列表
import CustomListAdd from "../components/CustomListAdd";//添加用户
import TaroClass from  "../components/TaroClass";//塔罗分类
import TaroClassAdd from  "../components/TaroClassAdd";//塔罗分类添加
import TaroVedio from  "../components/TaroVedio";//塔罗视频
import TaroVedioAdd from  "../components/TaroVedioAdd"; //塔罗视频添加
import AdminList from  "../components/AdminList";//管理员列表
import AdminListAdd from "../components/AdminListAdd";//添加管理员管理员列
import IntroList from "../components/IntroList";//介绍列表
import IntroListAdd from "../components/IntroListAdd";//添加介绍表
import CardDay from "../components/CardDay";//添加今日卡牌
vue.use(Router);

export default new Router({
    routes : [
        {
            path : '/Index',
            component : HelloWorld ,
            name : 'Index'
        },
        {
            path : '/Card' ,
            component : Card ,
            name : 'Card'
        },
        {
            path : '/CardAdd/:id' ,
            component : CardAdd ,
            name : 'CardAdd'
        },
        {
            path : '/CardArray' ,
            component : CardArray ,
            name : 'CardArray'
        },
        {
            path : '/CardArrayAdd/:id' ,
            component : CardArrayAdd ,
            name : 'CardArrayAdd'
        },
        {
            path : '/CustomList' ,
            component : CustomList ,
            name : 'CustomList'
        },
        {
            path : '/CustomListAdd' ,
            component : CustomListAdd ,
            name : 'CustomListAdd'
        },
        {
            path : '/TaroClass' ,
            component : TaroClass ,
            name : 'TaroClass'
        },
        {
            path : '/TaroClassAdd' ,
            component : TaroClassAdd ,
            name : 'TaroClassAdd'
        },
        {
            path: '/TaroVedio',
            component: TaroVedio,
            name: 'TaroVedio'
        },
        {
            path: '/TaroVedioAdd/:id',
            component: TaroVedioAdd,
            name: 'TaroVedioAdd'
        },
        {
            path: '/AdminList',
            component: AdminList,
            name: 'AdminList'
        },
        {
            path: '/AdminListAdd',
            component: AdminListAdd,
            name: 'AdminListAdd'
        },
        {
            path: '/IntroList',
            component: IntroList,
            name: 'IntroList'
        },
        {
            path: '/IntroListAdd/:id',
            component: IntroListAdd ,
            name: 'IntroListAdd'
        },
        {
            path: '/CardDay',
            component: CardDay,
            name: 'CardDay'
        }
    ]
})
